import { Component } from '@angular/core';
import { IonicPage, NavController, AlertController } from 'ionic-angular';

@IonicPage()
@Component({
  selector: 'page-list-contract',
  templateUrl: 'list-contract.html',
})
export class ListContractPage {

  contracts = [
    { ContractName: 'CoffeeJan18', chosse: true },
    { ContractName: 'CoffeeJan18', chosse: true },
    { ContractName: 'CoffeeJan18', chosse: false },
    { ContractName: 'CoffeeJan18', chosse: false },
    { ContractName: 'CoffeeJan18', chosse: true },
    { ContractName: 'CoffeeJan18', chosse: true },
    { ContractName: 'CoffeeJan18', chosse: false },
    { ContractName: 'CoffeeJan18', chosse: true }
  ];

  constructor(public navCtrl: NavController, public alertCtrl: AlertController) {
  }

  private isSearchOn: boolean = false;
  toggleDetails() {
    this.isSearchOn = !this.isSearchOn;
  }
  ionViewDidLoad() {
    console.log('ionViewDidLoad ListContractPage');
  }

  showConfirm(contractCode: any) {
    const confirm = this.alertCtrl.create({
      title: 'Bạn có chắc muốn bỏ chọn?',
      buttons: [
        {
          text: 'Hủy bỏ',
          handler: () => {
            console.log('Disagree clicked' + contractCode);
          }
        },
        {
          text: 'Đồng ý',
          handler: () => {
            console.log('Agree clicked' + contractCode);
          }
        }
      ]
    });
    confirm.present();
  }

}



